import './App.css';
import logo from './images/logo.png'
import React, {useState} from 'react'


function App() {
    const [text, setText] = useState('');
    const [showError, setShowError] = useState('')
    const [showResult, setShowResult] = useState('')
    const [error, setError] = useState('')
    const [result, setResult] = useState('')

    const countries = new Map()
    countries.set('CAN', ['CAN'])
    countries.set('MEX', ['MEX'])
    countries.set('BLZ', ['MEX','BLZ'])
    countries.set('GTM', ['MEX', 'GTM'])
    countries.set('SLV', ['MEX', 'GTM','SLV'])
    countries.set('HND', ['MEX', 'GTM','HND'])
    countries.set('NIC', ['MEX', 'GTM', 'HND','NIC'])
    countries.set('CRI', ['MEX', 'GTM', 'HND', 'NIC','CRI'])
    countries.set('PAN', ['MEX', 'GTM', 'HND', 'NIC', 'CRI','PAN'])


    var pattern = /^[A-Z]{3}$/i

    function validateText() {
        var testText = text.toUpperCase()

        if (pattern.test(testText)) {

            if (countries.has(testText)) {
                showResultMessage(countries.get(testText))
            }
            else {
                if (testText == "USA") {
                    showErrorMessage("Pick country other than the USA given you are already there.")
                }
                else {
                    showErrorMessage("Country code not in North America, enter North American country code.")
                }
            }

        }   
        else {

            showErrorMessage("Formatting of input is wrong, enter a 3-letter country code in North America.")

        }
    }

    function showErrorMessage(errorInfo) {
        setShowError(true)
        setShowResult(false)
        setError("ERROR: " + errorInfo)
    }

    function showResultMessage(resultArray) {
        var result = "Countries to visit: USA"
        for (let i in resultArray)
        {
            result = result.concat(" => ")
            result = result.concat(resultArray[i])
        }
        setShowError(false)
        setShowResult(true)
        setResult(result)
    }


  return (
      <section className="App-header">
          <img src={logo} alt="logo for website." className="App-logo"></img>
          <h1 >Enter a 3-letter country code destination to travel to from the United States</h1>
          <div>
              <input type="text" value={text} autoFocus onChange={sendText => setText(sendText.target.value)}></input>
          </div>
          <div>
              <button className="button" onClick={validateText}>Display Countries</button>
          </div>
          <div style={{ display: showResult ? "block" : "none" }} >{result}</div>
          <div style={{ color: '#ff0000', display: showError ? "block" : "none" }} >{error}</div>
          {/*<div style={{ color: '#ff0000', display: showError ? "block" : "none" }} >ERROR: Format of input isn't right, use 3-letter country code in North America.</div>*/}
      </section>
  );
}

export default App;
